import React, { useState, useEffect, useRef } from "react";
import styled from "styled-components";
import { Wrapper, Skeleton } from "./Products.styles";
import Product from "components/organisms/Product/Product";
import { StoreContext } from "providers/StoreProvider";
import gsap from "gsap";
import axios from "axios";

export const query = `
{
  allProducts(filter: {category: {matches: {pattern: "Shirts"}}}) {
    id
    name
    model
    price
  }
}
      `;

const Shirts = () => {
  const [shirts, setShirts] = useState([]);

  useEffect(() => {
    axios
      .post(
        "https://graphql.datocms.com/",
        {
          query,
        },
        {
          headers: {
            authorization: `Bearer ${process.env.REACT_APP_DATOCMS_KEY}`,
          },
        }
      )
      .then(({ data: { data } }) => {
        setShirts(data.allProducts);
      })
      .catch((err) => {
        alert(err);
      });
  }, []);

  return (
    <>
      <Wrapper>
        {shirts.length == 0 ? (
          <>
            <Skeleton className="pulse"></Skeleton>
            <Skeleton className="pulse"></Skeleton>
            <Skeleton className="pulse"></Skeleton>
          </>
        ) : (
          shirts.map((item) => <Product product={item} key={item.id} />)
        )}
      </Wrapper>
    </>
  );
};

export default Shirts;
